body{ 
  font-family: 'Montserrat', sans-serif;
   background: #F7F7F7;
}

.center {
  margin: auto;
  text-align: center;
}

.clear{
  height: 10vh;
}

.login-container{
  /* position: absolute; */
  /* top: 0; */
  /* height: 100vh;  */
  /* width: 100vw;  */
  /* background-color: #000; */
  background: url(/static/media/bg.ac640be9.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  /* max-width: 100vw; */
  /* max-height: 100vh; */
  /* overflow: hidden; */
}

.login-container h1 {
  color: #fff;
  font-weight: bolder;
  font-size: 48px;
}

.login{
  padding: 250px 16px 16px 16px;
  /* background: url('../img/bg.jpg'); */
  /* background-position: top; */
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
  width: 100vw ;
  height: 100vh;
  overflow: hidden !important;
}


.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  top:0;
  height: 10vh;
  width: 100vw;
  z-index: 1000;
  background: #F7F7F7;
}

.logo {
  height: 4vh;
  width: 30vw;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 0;
  /* left:0; */
  background-color: #000;
  width: 100%;
  height: 7vh;
  border-radius: 20px 20px 0 0;
  color: #fff;
  z-index: 1000;
}

.cat-group {
  padding: 16px;
  width: 100vw;
  background: #F7F7F7;
  position: fixed;
  bottom: 8%;
  left: 0;
  list-style: none;
}

.cat-group-item {
  display: flex;
  height: 7vh;
  justify-content: flex-start;
  background: #fff;
  border: 2px solid #F7F7F7;
  border-radius: 10px;
  margin: 6px;
  padding: 12px;
}


.top-card {
  background: #000;  
  border-radius: 10px;
  background-size: cover;
  margin: 12px auto;
  width: 90vw;
  height: 35vh;
}

.top-card img {
  width: 100%;
  height: 100%;
  border-radius: 25px;
}


/* .popular-container {
  display: flex;
}

.popular {
  display: flex;
  flex-direction: column;
  height: 20vh;
  width: 35vw;
  margin: 12px;
  background: #fff;
  border-radius: 10px;
} */

.popular-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.popular {
  padding: 12px;
  margin: 6px;
  background: #fff;
  border-radius: 25px;
  text-align: center;
}

.popular img {
  height: 25vh;
  width: 35vw;
  margin-bottom: 6px;
  border-radius: 10px;
}

.popular .info {
  align-items: center;
  padding: 2px;
}

.popular .info, h3 {
  color: grey;
  font-size: 14px;
}

.view-more button {
  border: none;
}

.shop {
  min-height: 10vh;
  /* width: 95vw; */
  margin: 6px;
  margin-top: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 15px;
}

.shop img {
  height: 85px;
  width: 85px;
  margin: 0px 24px;
  border-radius: 5px;
  margin-top: -15px;
}

.shop .info h3{
  color: #000;
  font-size: 14px;
}

.shop .info h4{
  color: #B0B0B0;
  font-size: 12px;
}

.signup{
  margin: 24px auto;
  padding: 12px 24px;
  background: #fff;
  min-height: 100vh;
  width: 80vw;
  font-weight: bold;
}

.signup input {
  background: #F7F7F7;
}

.signup .heading{
  padding: 16px 0px;
  text-align: center;
  font-size: 2.5rem;
}

.est span{
  font-size: 12px;
  margin-left: 4px;
  padding-top: 6px;
}

.est img {
  height: 18px;
  width: 18px;
}

.yellow{
  background: #FFDD70;
  margin-top: 16px;
  padding: 6px;
  width: 85vw;
  border-radius: 15px;
}

.yellow h4{
  padding-left: 12px;
  font-weight: bold;
}

.yellow p{
  padding-left: 12px;
}

.order-single {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.total {
  position: fixed;
  bottom: 12px;
  left:10vw;  
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #000;
  color: #fff;
  margin:16px 0px;
  padding: 8px;
  border-radius: 15px;
  width: 80vw;
  z-index: 222;
}

.total img {
  color: white;
}

.total button {
  color: white;
  background-color: black;
  margin-right: 12px;
  border: none;
  border-radius: 12px;
  padding: 6px;
}

.total button:hover {
  color: black;
  background-color: white;
}

.brand {
  background: #fff;
  width: 50vw;
  text-align: center;
  margin: auto;
}

.search-box{
  width: 90vw;
  margin: 2vh 5vw;
  border-radius: 15px;
}

.search-result {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #fff;
  border-radius: 15px;
  padding: 16px;
  margin: 6px 16px;
  width: 70vw;
  margin: 12px auto;
}

.search-result img {
  height: 3vh;
  margin: 6px;
}

.search-result p {
  font-weight: bold;
}

.view-more{
  display: flex;
  justify-content: space-around;
}

.view-more button{
  background: black;
  color: white;
  margin: 12px;
  margin: 12px;
  border-radius: 15px;
  padding: 8px;
}

.confirm {
  width: 80vw;
}

.product-info {
  margin: 5vh 5vw;
}

.email-input {
  width: 50vw;
  margin: 25vh 25vw 0 25vw;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.token-input {
  /* width: 50vw; */
  margin: 25vh 25vw 0 25vw;
  /* margin: 25vh 10vw 0 10vw; */
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.steps-content {
  margin-top: 16px;
  border: 1px dashed #e9e9e9;
  border-radius: 2px;
  background-color: #fafafa;
  min-height: 200px;
  /* text-align: center; */
  padding: 40px 20px;
}

.steps-action {
  margin-top: 24px;
}
@media screen and (min-width: 660px) {
  .login {
    width: 50vw;
    margin-left: 25vw;
    padding-top: 20vh;
  }

  .login h1 {
    font-size: 3.8rem;
  }

  .signup {
    width: 50vw;
  }

  /* body{
    background-color: lawngreen;
  } */
  .header {
    height: 7vh;
  }

  .logo {
    width: 150px;
  }

  .top-card {
    width: 60vw;
  }

  .popular-container {
    width: 80vw;
  }

  .popular {
    padding: 12px;
    margin: 6px;
    background: #fff;
    border-radius: 25px;
    text-align: center;
  }
  
  .popular img {
    height: 20vh;
    width: 20vw;
    margin-bottom: 6px;
    border-radius: 10px;
  }
  
  .popular .info {
    align-items: center;
    padding: 2px;
  }
  
  .popular .info, h3 {
    color: grey;
    font-size: 14px;
  }

  .shop-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .shop {
    margin: 24px 24px;
    padding: 6px 16px 6px 0;
  }

  .navbar {
    width: 70vw;
    margin-left: 15vw;
  }

  .cart {
    width: 70vw;
    margin: auto;
  }
  
  .yellow {
      width: 65vw;
  }

  .total{
    margin: 24px 0;
    left: 15vw;
    width: 70vw;
  }

  .confirm {
    width: 70vw;
  }

  .search-box{
    width: 70vw;
    margin: 2vh 15vw;
  }

  .product-info {
    width: 50vw;
    margin: 5vh auto;
  }

  .token-input  {
    width: 50vw;
    margin: 25vh 25vw 0 25vw;
  }

  .email-input  {
    margin: 25vh 10vw 0 10vw;
  }
}
