@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');


body{ 
  font-family: 'Montserrat', sans-serif;
   background: #F7F7F7;
}

.center {
  margin: auto;
  text-align: center;
}

.clear{
  height: 10vh;
}

.login-container{
  /* position: absolute; */
  /* top: 0; */
  /* height: 100vh;  */
  /* width: 100vw;  */
  /* background-color: #000; */
  background: url('../img/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  /* max-width: 100vw; */
  /* max-height: 100vh; */
  /* overflow: hidden; */
}

.login-container h1 {
  color: #fff;
  font-weight: bolder;
  font-size: 48px;
}

.login{
  padding: 250px 16px 16px 16px;
  /* background: url('../img/bg.jpg'); */
  /* background-position: top; */
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
  width: 100vw ;
  height: 100vh;
  overflow: hidden !important;
}


.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  top:0;
  height: 10vh;
  width: 100vw;
  z-index: 1000;
  background: #F7F7F7;
}

.logo {
  height: 4vh;
  width: 30vw;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 0;
  /* left:0; */
  background-color: #000;
  width: 100%;
  height: 7vh;
  border-radius: 20px 20px 0 0;
  color: #fff;
  z-index: 1000;
}

.cat-group {
  padding: 16px;
  width: 100vw;
  background: #F7F7F7;
  position: fixed;
  bottom: 8%;
  left: 0;
  list-style: none;
}

.cat-group-item {
  display: flex;
  height: 7vh;
  justify-content: flex-start;
  background: #fff;
  border: 2px solid #F7F7F7;
  border-radius: 10px;
  margin: 6px;
  padding: 12px;
}


.top-card {
  background: #000;  
  border-radius: 10px;
  background-size: cover;
  margin: 12px auto;
  width: 90vw;
  height: 35vh;
}

.top-card img {
  width: 100%;
  height: 100%;
  border-radius: 25px;
}


