@media screen and (min-width: 660px) {
  .login {
    width: 50vw;
    margin-left: 25vw;
    padding-top: 20vh;
  }

  .login h1 {
    font-size: 3.8rem;
  }

  .signup {
    width: 50vw;
  }

  /* body{
    background-color: lawngreen;
  } */
  .header {
    height: 7vh;
  }

  .logo {
    width: 150px;
  }

  .top-card {
    width: 60vw;
  }

  .popular-container {
    width: 80vw;
  }

  .popular {
    padding: 12px;
    margin: 6px;
    background: #fff;
    border-radius: 25px;
    text-align: center;
  }
  
  .popular img {
    height: 20vh;
    width: 20vw;
    margin-bottom: 6px;
    border-radius: 10px;
  }
  
  .popular .info {
    align-items: center;
    padding: 2px;
  }
  
  .popular .info, h3 {
    color: grey;
    font-size: 14px;
  }

  .shop-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .shop {
    margin: 24px 24px;
    padding: 6px 16px 6px 0;
  }

  .navbar {
    width: 70vw;
    margin-left: 15vw;
  }

  .cart {
    width: 70vw;
    margin: auto;
  }
  
  .yellow {
      width: 65vw;
  }

  .total{
    margin: 24px 0;
    left: 15vw;
    width: 70vw;
  }

  .confirm {
    width: 70vw;
  }

  .search-box{
    width: 70vw;
    margin: 2vh 15vw;
  }

  .product-info {
    width: 50vw;
    margin: 5vh auto;
  }

  .token-input  {
    width: 50vw;
    margin: 25vh 25vw 0 25vw;
  }

  .email-input  {
    margin: 25vh 10vw 0 10vw;
  }
}